.content {
    width: 300px;
    height: 600px;
    border-radius: 20px;
    background-color: rgba(225, 225, 225, 0.9);
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
}
  
.pages--container {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    Ensurechildelementsdonotoverflowbackground-color: rgba(65, 63, 63);
    /*Fallbackcolor*/display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
  
  /* Create a blur effect using a pseudo-element */
  .pages--container::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    filter: blur(10px); /* Adjust the blur amount as needed */
    z-index: -1; /* Ensure the blur layer is behind the content */
  }

  .content{
    padding: 0px 40px;
    height: auto;
  }
  .isicontent{
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid rgb(151, 151, 151);
  }

  .go-to-btn:hover{
    cursor: pointer;
  }
  .go-to-btn{
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}


.pages--container {
  position: relative;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}

.pages--container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('./IMG_1541.JPG');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  filter: blur(20px);
  -webkit-filter: blur(20px);
}

.content, .profile {
  position: relative;
  z-index: 1; /* Ensure content is above the blurred background */
}


.profile{
  background-image: url('./IMG_1541.JPG');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 200px;
  height: 200px;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  margin-bottom: 20px;
}

.name{
  font-weight: bold;
  font-size: 20px;
  letter-spacing: 2px;
  padding: 0;
  margin: 0;
  color: white;
}

.desc{
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.mb{
  padding: 0;
  margin: 0;
}

.descc{
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

@media (max-width: 600px) {
  .content{
    width: 250px;
    
  }
}